import React, { useState } from "react"
import { NavLink } from "react-router-dom"

const Project = ({name, year, description, img, link, aboutPage, sourceCode}) => {
    const [isHovering, setIsHovering] = useState(false)
    
    return (
        
        <div className="relative bg-foreground 2xl:w-[640px] 2xl:h-[340px] lg:w-[450px] lg:h-[239px] w-[320px] h-[170px] my-4 mx-auto drop-shadow" onMouseEnter={() => setIsHovering(true)} onMouseLeave={() => setIsHovering(false)}>
            {/* <h1>test</h1> */}
            
            
            {/* Hover */}
            <div className={"absolute grid grid-rows-2 w-full p-2 lg:p-6 2xl:p-12 h-full bg-black " + (isHovering ? 'opacity-75 animate-fadeIn' : 'opacity-0 animate-fadeOut')} >
                <p className="text-white text-sm lg:text-lg">{description}</p>
                
                {sourceCode ? 
                    <div className="grid grid-cols-2 text-center mt-auto">
                        <a className='text-white text-xl' href={link} target="_blank" rel="noopener noreferrer">Go to Project</a>
                        <a className='text-white text-xl' href={sourceCode} target="_blank" rel="noopener noreferrer">Source Code</a>
                    </div>
                :
                
                (link &&
                <div className="text-center mt-auto">
                    <a className='text-white text-xl' href={link} target="_blank" rel="noopener noreferrer">Go to Project</a>
                </div>)
                }

                {aboutPage &&
                (<div className="text-center mt-auto">
                    <NavLink className='text-white text-xl' to={aboutPage}>About</NavLink>
                </div>)}
                
            </div>

            <div className="h-full grid grid-rows-6 text-center">
                <img className="mx-auto my-auto w-8/12 row-span-5" src={'./' + img}></img>
                <h2 className="font-ivy-semi-bold lg:text-2xl 2xl:text-4xl text-xl">{name}</h2>
            </div>

            {/* Year */}
            <h3 className="lg:text-3xl text-2xl font-neil-bold absolute -left-7 lg:-left-8 -top-0 -rotate-90 translate translate-y-10">{year}</h3>
        </div>
    )
}

const Projects = () => {
    const projects = [
        {
            name: 'Solar Agribot',
            year: '2022',
            description: 'Solar Agribot is my on going Major Qualifying Project for WPI. My Team and I are building an agricultural robot to autonomously drive around a field and collect soil samples. Additionally while providing a easy way for farmers to view this data.',
            img: 'solar_agribot.png',
            aboutPage: '/solar-agribot'
        },
        {
            name: 'Rock Climbing Route Visualization',
            year: '2022',
            description: 'This website was part of my final project for CS4802 - Biovisualization. The page showcases the hold choice of two different climbers in real time, allowing for easier comparison.',
            link: 'https://travisnthompson.com/climbing-route-vis',
            sourceCode: 'https://github.com/TravisThomp/final',
            img: 'climbing_route_vis.png'
        },
        {
            name: 'I\'m Okay',
            year: '2022',
            description: 'This website was a fun project I decided to make when I had covid. I created it to to play around with d3, and its ability to create animations.',
            link: 'https://travisnthompson.com/im-okay',
            sourceCode: 'https://github.com/TravisThomp/im-okay',
            img: 'im_okay.png'
        },
        {
            name: 'Satellites in Space',
            year: '2020',
            description: 'This website was part of a fun project I took on to better understand online 3D software. The page visualizes all the satellites currently orbiting around earth using public TLE data.',
            link: 'http://travisnthompson.com/satellites-in-space',
            sourceCode: 'https://github.com/TravisThomp/WorldFromSpace',
            img: 'satellites_in_space.png'
        },
        {
            name: 'Ban Animations',
            year: '2017',
            description: 'Ban Animations is a server side game plugin for Minecraft with over 20,000 downloads. It allows server administrators to ban players while playing a custom animation around them.',
            link: 'http://travisnthompson.com/ban-animations',
            img: 'ban_animations.png'
        },
    ]

    return (
        <div id='projects' className="min-h-screen bg-background text-cream">
            <h1 className='text-center font-ivy-bold-italic text-dark-gray my-6 lg:text-8xl 2xl:text-9xl text-5xl'>PROJECTS</h1>
            <div className="grid sm:grid-cols-1 lg:grid-cols-2">
            {projects.map(project => <Project name={project.name} year={project.year} description={project.description} img={project.img} link={project.link} aboutPage={project.aboutPage} sourceCode={project.sourceCode}/>)}

            </div>
        </div>
    )
}

export default Projects