import React from 'react'
import { NavLink } from 'react-router-dom'

const Title = ( {title} ) => {
    return (
        <h1 className='font-ivy-bold-italic text-5xl text-center pb-5 text-cream '>{title}</h1>
    ) 
}

const Section = ( {heading, content} ) => {
    return (
        <>
            <h2 className='font-ivy-bold-italic text-4xl text-ce py-5 text-cream '>{heading}</h2>
            {content}
        </>
    )
}

const SubSectionHeading = ( {title} ) => {
    return <h3 className='text-2xl font-ivy-bold-italic py-5 text-cream'>{title}</h3>
}

const Paragraph = ( {text} ) => {
    return <p className='pb-5 text-lg indent-10 text-cream '>{text}</p>
}

const Image = ( {link, heading} ) => {
    return (
        <div className='w-full bg-foreground py-2 rounded-md mb-5'>
            <div className='w-10/12 mx-auto m-5 bg-foreground'>
                <img src={link}></img>
            </div>
        </div>
    )
}



const SolarAgribot = () => {
    window.scrollTo(0, 0)
    return (
        <>
        <NavLink to='/'>
            <h4 className='text-center my-2 text-cream'>Back to Projects</h4>
        </NavLink>
        <div className='lg:w-8/12 md:w-full mx-auto mb-10 p-3'>

            <Title title='Solar Agribot'/>
            {/* Project Overview */}
            <Section heading='Project Overview' content={
                [
                    <Paragraph text='For my Major Qualifying Project, a group of eight and I are working on designing and implementing an agricultural robot to autonomously collect soil samples and fertilize rows of corn. The project began in early September and will be finalized by the end of April. Since I’m a double major I have been working with the electrical team on designing the electrical system and also working on the programming as the sole computer science major.'/>,
                    <Image link='/solar-agribot/robot-mechanical.png' heading='Robot Design'/>,

                    <Paragraph text='The robot consists of a rocker suspension, a soil probe, navigation system, communication system, and a fertilization system. The robot navigation system is using an RTK device to get exact robot positioning, and a LiDar camera for collision detection. The communication system consists of a LoRa transmitter that will communicate with our robot’s hub. The fertilization system consists of a water level detector and a pump to disperse our fertilizer.'/>
                ]
            }/>

            <Section heading="My Work" content={
                [
                    <SubSectionHeading title='Website'/>,
                    <Paragraph text='The websites allows the data our robot collects to easily become available to the farmer. To design this website I used Adobe XD allowing me to create a quick prototype of the website, and to trial different design iterations. The mock website design can be seen below.'/>,
                    <Image link='/solar-agribot/website-design.png'  heading='Website Design'/>,
                    
                    <Paragraph text={'The current website is still in development and it\'s current state can be seen below. The website was made using React for it\'s component state management system. The map was created using the Google Maps API, allowing for the farmer to click on each sample at a specific location on the field. The website load data from our webserver using Https Requests.'}/>,
                    <Image link='/solar-agribot/website-current.png' heading='Current Website'/>,

                    <SubSectionHeading title='Web Server'/>,
                    <Paragraph text={'The purpose of the webserver is to send data to our website, and communicate with our hub server. The server interacts with an MongoDB cloud server to store the farmer\'s soil data. This was made using NodeJS, with Express.JS Libraries. The entire movement of data for our robot can be seen below'}/>,
                    <Image link='/solar-agribot/communications-diagram.png' heading='Robot Data Movement'/>,

                    <SubSectionHeading title='Hub Server'/>,
                    <Paragraph text={'The purpose of the hub server is to directly communicate with our robot and to our web server. The hub server runs off of a Raspberry Pi that will live in the robot\'s hub. The hub server will send commands to the robot, telling it when to go out and come back. The robot in addition will send soil data collected to the hub server. The robot - hub server communication will take place using a transceiver connected to an Arduino nano. The Raspberry PI will communicate over serial messages back and forth with the arduino. The hub server in addition send data to the web server, allowing for quick access to collected soil data.'}/>,

                    <SubSectionHeading title='Electrical System'/>,
                    <Paragraph text={'In addition to the programming I have also been directly involved in designing the robot\'s electrical system. The system is separated into sections controlled by their own Arduino Teensy. The ability to separate each section into it\'s own micro controller allows for a modular design. This meaning that each section can be built and fully tested on it\'s own before going onto the robot. The full electrical layout can be seen below.'}/>,
                    <Image link='/solar-agribot/electrical-design.png' heading='Robot Electrical Design'/>,

                ]
            }>
            </Section>
        </div>
        </>
    )
}

export default SolarAgribot